html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 14px;
  line-height: 1.4;
}

.triangle-slider {
  height: 600px;
  width: 900px;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.triangle-slider__piece {
  position: absolute;
  overflow: hidden;
  left: -50px;
  right: -50px;
}

.triangle-slider__piece:nth-child(1) {
  height: 50%;
  z-index: 1;
}

.triangle-slider__piece:nth-child(1) .triangle-slider__image {
  background-position: center 0;
}

.triangle-slider__piece:nth-child(2) {
  height: 50%;
  top: 25%;
  bottom: 25%;
  transform: rotate(-10deg);
  width: auto;
  z-index: 2;
}

.triangle-slider__piece:nth-child(2) .triangle-slider__image {
  background-position: center -50px;
  transform: rotate(10deg);
}

.triangle-slider__piece:nth-child(3) {
  bottom: 0;
  height: 50%;
  z-index: 1;
}

.triangle-slider__piece:nth-child(3) .triangle-slider__image {
  background-position: center -200px;
}

.triangle-slider__wrapper {
  height: 100%;
}

.triangle-slider__slide {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.triangle-slider__slide:nth-child(2) {
  transform: translateX(100%);
}

.triangle-slider__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-repeat: no-repeat;
}

.slider {
  max-width: 900px;
  height: 600px;
  position: relative;
  overflow: hidden;
}

.slider__super-wrapper {
  position: absolute;
  width: 100%;
  height: 600px;
  top: 0;
  left: 0;
}

.slider__1 {
  clip-path: url(#myPath1);
}

.slider__2 {
  clip-path: url(#myPath2);
}

.slider__3 {
  clip-path: url(#myPath3);
}

.slider__wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
}

.a .slider__wrap {
  transform: translate(-100%, 0);
}

.slider__current-slide, .slider__new-slide {
  position: absolute;
  width: 100%;
  height: 600px;
}

.clip-1 {
  clip-path: url(#myPath1);
}

.clip-2 {
  clip-path: url(#myPath2);
}

.clip-3 {
  clip-path: url(#myPath3);
}

.mask {
  width: 100%;
  position: absolute;
}
